<template>
  <div class="bg-primary-subtle p-3 m-2">
    <h2 class="text-center m-2">Employees list</h2>

    <!-- Таблица с уже созданными сотрудниками -->
    <table class="table bg-light-subtle mb-0 mt-4" v-if="employees.length > 0">
      <thead>
        <tr class="text-center">
          <th scope="col">N</th>
          <th scope="col">Firstname</th>
          <th scope="col">Lastname</th>
          <th scope="col">Experience</th>
          <th scope="col">Age</th>
          <th scope="col">Address</th>
          <th scope="col">Actions</th>
        </tr>
      </thead>

      <tbody>
        <transition-group name="employee-list">
          <tr
            v-for="employee of employees"
            :key="employee.id"
            class="text-center"
          >
            <!-- @update="$emit('update', employee)" -->
            <th scope="row">{{ employee.id }}</th>
            <td>
              <input
                class="text-center form-control"
                v-if="employee.editmode"
                v-model="employee.firstname"
              />
              <div v-else>{{ employee.firstname }}</div>
            </td>
            <td>
              <input
                class="text-center form-control"
                v-if="employee.editmode"
                v-model="employee.lastname"
              />
              <div v-else>{{ employee.lastname }}</div>
            </td>
            <td>
              <input
                class="text-center form-control"
                v-if="employee.editmode"
                v-model="employee.experience"
              />
              <div v-else>{{ employee.experience }}</div>
            </td>
            <td>
              <input
                class="text-center form-control"
                v-if="employee.editmode"
                v-model="employee.age"
              />
              <div v-else>{{ employee.age }}</div>
            </td>
            <td>
              <input
                class="text-center form-control"
                v-if="employee.editmode"
                v-model="employee.address"
              />
              <div v-else>{{ employee.address }}</div>
            </td>
            <td>
              <!-- <RouterLink
                class="btn bg-success-subtle"
                @click="$emit('editEmp', employee), openModalWindowEdit($event)"
                :to="{ path: '/employees/' + employee.id + '/edit' }"
                >Edit</RouterLink
              > -->

              <div
                v-if="employee.editmode"
                class="d-flex justify-content-center gap-2"
              >
                <button
                  class="btn border border-2 border-success border-opacity-50 text-success text-opacity-75"
                  @click="save(employee)"
                >
                  Save
                </button>
                <button
                  class="btn border border-2 border-danger-subtle border-opacity-25 text-danger text-opacity-50"
                  @click="cancel(employee)"
                >
                  Cancel
                </button>
              </div>
              <div v-else class="d-flex justify-content-center gap-2">
                <button
                  class="btn border border-2 border-info border-opacity-50 bg-info-subtle text-info"
                  @click="edit(employee)"
                >
                  <!-- @click="$emit('editEmp', employee), openModalWindowEdit($event)" -->
                  Edit
                </button>
                <button
                  class="btn border border-2 border-danger border-opacity-25 bg-danger-subtle text-danger text-opacity-50"
                  @click="$emit('remove', employee)"
                >
                  Delete
                </button>
                <button
                  @click="$router.push(`/employees/${employee.id}`)"
                  class="btn"
                >
                  Open
                </button>
              </div>
            </td>
          </tr>
        </transition-group>
      </tbody>
    </table>
    <div v-else class="text-center text-danger bg-body-tertiary p-4 mt-4">
      Table is empty
    </div>
  </div>

  <!-- Вызов модального окна и попытка отправки массива employees в компонент EmployeesFormEdit -->
  <!-- <modalwindow-edit v-model:show="modalWindowEditVisible">
    <employees-form-edit
      @editEmp="getEmployeeData"
      @update="updateEmployee"
      :employees="employees"
    />
  </modalwindow-edit> -->
</template>

<script>
// import ModalwindowEdit from "@/components/UI/ModalWindowEdit.vue";
// import EmployeesFormEdit from "@/components/EmployeesFormEdit.vue";

export default {
  // components: {
  //   ModalwindowEdit,
  //   EmployeesFormEdit,
  // },

  props: {
    employees: { type: Array, required: true },
  },
  emits: ["remove"],

  data() {
    return {
      modalWindowEditVisible: false,
      editmode: false,
    };
  },

  methods: {
    edit: function (employee) {
      employee.editmode = !employee.editmode;
    },
    cancel: function (employee) {
      employee.firstname = this.employees[employee.id - 1].firstname;
      employee.editmode = !employee.editmode;
    },
    save: function (employee) {
      employee.firstname = this.employees[employee.id - 1].firstname;
      employee.editmode = !employee.editmode;
    },
    openModalWindowEdit() {
      console.log(this.employee);
      this.modalWindowEditVisible = true;
    },
    updateEmployee(employee) {
      console.log(employee);
      this.employees.push(employee);
      this.modalWindowEditVisible = false;
    },
  },
};
</script>

<style scoped>
.employee-list-item {
  display: inline-block;
  margin-right: 10px;
}
.employee-list-enter-active,
.employee-list-leave-active {
  transition: all 0.5s ease;
}
.employee-list-enter-from,
.employee-list-leave-to {
  opacity: 0;
  transform: translateX(130px);
}
.employee-list-move {
  transition: transform 0.5s ease;
}
</style>
