<template>
  <div>
    <div class="d-flex flex-column my-4">
      <h2 class="text-center">Employees Page</h2>

      <!-- Поле для поиска сотрудника по Фамилии -->
      <div class="input-group my-5">
        <input
          type="text"
          class="form-control"
          placeholder="Employee's Lastname..."
          v-model="searchQuery"
        />
        <button
          class="btn btn-outline-secondary"
          type="button"
          id="button-addon2"
        >
          Search
        </button>
      </div>

      <div class="d-flex justify-content-between align-items-center">
        <!-- Сортировка по Id, Имени, Фамилии, Стажу, Возрасту и Адресу -->
        <sort-employees v-model="selectedSort" :options="sortOptions" />

        <!-- Кнопка для открытия формы регистрации нового сотрудника -->
        <button class="btn btn-primary" @click="openModalWindow">
          Add employee
        </button>
      </div>
    </div>

    <!-- Окно создания нового сотрудника  -->
    <modal-window v-model:show="modalWindowVisible">
      <employees-form @create="saveEmployee" :employees="employees" />
    </modal-window>

    <!-- Таблица с уже созданными сотрудниками -->
    <employees-list
      :employees="sortedAndSearchedEmployees"
      @remove="removeEmployee"
    />
  </div>
</template>

<script>
import EmployeesForm from "@/components/EmployeesForm.vue";
import EmployeesList from "@/components/EmployeesList.vue";
import ModalWindow from "@/components/UI/ModalWindow.vue";
import SortEmployees from "@/components/UI/SortEmployees.vue";

export default {
  components: {
    EmployeesForm,
    EmployeesList,
    ModalWindow,
    SortEmployees,
  },
  data() {
    return {
      employees: [
        {
          id: 1,
          firstname: "Frodo",
          lastname: "Beggins",
          experience: 1,
          age: 20,
          address: "Shire",
        },
        {
          id: 2,
          firstname: "Anton",
          lastname: "Ivanov",
          experience: 4,
          age: 19.5,
          address: "Russia, Moscow",
        },
        {
          id: 3,
          firstname: "Mary",
          lastname: "Thompson",
          experience: 12.3,
          age: 38,
          address: "15 Shipman Str., Toronto, Canada",
        },
        {
          id: 4,
          firstname: "Olga",
          lastname: "Bulgakova",
          experience: 6.2,
          age: 30,
          address: "Serbia, Belgrade, Preshevska 42",
        },
      ],
      modalWindowVisible: false,
      selectedSort: "",
      searchQuery: "",
      sortOptions: [
        { value: "id", name: "by ID" },
        { value: "firstname", name: "by Firstname" },
        { value: "lastname", name: "by Lastname" },
        { value: "experience", name: "by Experience" },
        { value: "age", name: "by Age" },
        { value: "address", name: "by Address" },
      ],
    };
  },

  methods: {
    openModalWindow() {
      this.modalWindowVisible = true;
    },
    saveEmployee(employee) {
      this.employees.push(employee);
      this.modalWindowVisible = false;
    },
    removeEmployee(employee) {
      if (confirm("Are you sure, you want to delete this employee❓"))
        this.employees = this.employees.filter((emp) => emp.id !== employee.id);
    },
  },
  computed: {
    sortedEmployees() {
      return [...this.employees].sort((employee1, employee2) => {
        if (
          typeof employee1[this.selectedSort] === "number" &&
          typeof employee2[this.selectedSort] === "number"
        ) {
          return employee1[this.selectedSort] - employee2[this.selectedSort];
        } else {
          return employee1[this.selectedSort]?.localeCompare(
            employee2[this.selectedSort]
          );
        }
      });
    },

    sortedAndSearchedEmployees() {
      return this.sortedEmployees.filter((employee) =>
        employee.lastname
          .toLocaleLowerCase()
          .includes(this.searchQuery.toLocaleLowerCase())
      );
    },
  },
};
</script>

