<template>
  <!-- Сортировка по Id, Имени, Фамилии, Стажу, Возрасту и Адресу -->
  <select
    class="form-select-sm border border-primary"
    v-bind="value"
    @change="changeOption"
  >
    <!-- v-model="modelValue" -->
    <option disabled selected value="">Sort employees</option>
    <option v-for="option in options" :key="option.value" :value="option.value">
      {{ option.name }}
    </option>
  </select>
</template>

<script>
export default {
  name: "sort-employees",
  props: {
    value: {
      type: String,
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    changeOption(event) {
      this.$emit("update:modelValue", event.target.value);
    },
  },
};
</script>

