<template>
  <div class="modal-window" v-if="show === true" @click.stop="hideModalWindow">
    <div class="modal-window__content" @click.stop>
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "modal-window",
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    hideModalWindow() {
      this.$emit("update:show", false);
    },
  },
};
</script>

<style scoped>
.modal-window {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(0, 0, 0, 0.5);
}
.modal-window__content {
  margin: auto;
  width: 80%;
  min-width: 300px;
  background: white;
  padding: 2rem 1rem;
  border-radius: 0.3rem;
}
</style>
