<template>
  <!-- Окно создания нового сотрудника  -->
  <div class="container w-100">
    <div class="card">
      <h4 class="card-header">Add new employee</h4>

      <form class="card-body" @submit.prevent>
        <div class="mb-3">
          <label>Firstname</label>
          <input
            type="text"
            class="form-control"
            v-bind:value="employee.firstname"
            @input="employee.firstname = $event.target.value"
          />
        </div>
        <div class="mb-3">
          <label>Lastname</label>
          <input type="text" class="form-control" v-model="employee.lastname" />
        </div>
        <div class="mb-3">
          <label>Experience</label>
          <input
            type="text"
            class="form-control"
            v-model.number="employee.experience"
          />
        </div>
        <div class="mb-3">
          <label>Age</label>
          <input
            type="text"
            class="form-control"
            v-model.number="employee.age"
          />
        </div>
        <div class="mb-3">
          <label>Address</label>
          <input type="text" class="form-control" v-model="employee.address" />
        </div>
        <!-- Кнопка сохранения нового сотрудника -->
        <button type="submit" class="btn btn-primary" @click="saveEmployee">
          Save
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      employee: {
        id: "",
        firstname: "",
        lastname: "",
        experience: "",
        age: "",
        address: "",
      },
    };
  },
  props: { employees: { type: Object, required: true } },
  methods: {
    saveEmployee() {
      if (this.employees.length > 0) {
        this.employee.id = this.employees[this.employees.length - 1].id + 1;
      } else {
        this.employee.id = 1;
      }
      this.$emit("create", this.employee);
      this.employee = {
        firstname: "",
        lastname: "",
        experience: "",
        age: "",
        address: "",
      };
    },
  },
};
</script>

